@import "../../css-config/mixins.scss";

.cart-panel {
    background-attachment: fixed;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    z-index: 10000;

    .opac-layer {
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .cart-content {
        width: 100%;
        height: 100%;
        background: white;
        position: relative;
        z-index: 100000;
        display: flex;
        flex-direction: column;
        transform: translateX(100%);
        animation: slideCartWindow 0.3s ease forwards;
        overflow-y: auto;

        @include md {
            width: 340px;
        }

        .cart-header {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding: 20px 15px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);

            .heading {
                flex-grow: 1;
                margin-bottom: 0;
                font-size: 20px;
                font-weight: 700;
                text-transform: uppercase;
            }

            .close-btn {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 5px;
                // color: #ef1400;
                cursor: pointer;

                svg {
                    font-size: 18px;
                }

                .text {
                    text-transform: uppercase;
                    font-size: 14px;
                    margin:0;
                }

                &:hover {
                    opacity: 0.5;
                }
            }
        }

        .empty-cart {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            margin-top: 100px;

            svg {
                font-size: 120px;
                opacity: 0.1;
            }

            .return-cta {
                outline: 0;
                border: 0;
                height: 40px;
                width: 150px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                font-size: 13px;
                color: white;
                background: #ef1400;
                border-bottom: 3px solid #c51605;
            }
        }

        .cart-footer{
            border-top:1px solid rgba(0, 0, 0, 0.1);
            .subtotal{
                padding: 20px 15px;
                border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                display: flex;
                justify-content:space-between;
                .text{
                    margin-bottom: 0;
                    font-size: 20px;
                    font-weight: 700;
                    text-transform: uppercase;
                    .total{
                        color: #8e2de2;
                    }
                }

            }
            .button{
                padding: 20px 15px;
                .checkout-cta{
                    outline: 0;
                    border: 0;
                    height: 50px;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    font-size: 16px;
                    color: white;
                    background: #ef1400;
                    border-bottom: 3px solid #c51605;
                }
            }
        }
    }
}


@keyframes slideCartWindow {
    0% {
        transform: translateX(100%);
    }

    100% {
        transform: translateX(0%);
    }

}