.footer1{width:100%;background:rgba(0,0,0,.9);background-repeat:no-repeat;background-size:cover}@media only screen and (min-width: 768px){.footer1{background-image:linear-gradient(315deg, rgba(64, 25, 0, 0.5) 0%, rgba(0, 0, 0, 0.7) 74%),url(../../assets/footer_background.png)}}.footer1 .footer-content{padding:50px 20px;max-width:1200px;margin:0 auto;flex-flow:wrap;gap:20px;left:0 !important;text-transform:uppercase;display:block}@media only screen and (min-width: 768px){.footer1 .footer-content{display:flex;justify-content:space-between;padding:50px 0}}.footer1 .footer-content .col1{max-width:300px}.footer1 .footer-content .col1:nth-child(3){width:150px}@media only screen and (min-width: 768px){.footer1 .footer-content .col1:nth-child(3){width:auto}}.footer1 .footer-content .col1 .title1{margin-bottom:20px;font-size:20px;color:#f01400}.footer1 .footer-content .col1 .text1{color:#fff;font-size:14px;transition:transform .3s}.footer1 .footer-content .col1 .text1:hover{transform:translateX(10px)}@media only screen and (min-width: 768px){.footer1 .footer-content .col1 .text1::before{content:"";margin-right:8px}}.footer1 .footer-content .col1 .c-item{display:flex;margin-bottom:10px;align-items:center}.footer1 .footer-content .col1 .c-item svg{flex-shrink:0;font-size:14px;margin-right:20px;margin-top:4px;color:#fff;transition:transform .3s}.footer1 .footer-content .col1 .c-item svg:hover{transform:translateY(10px)}.footer1 .footer-content .col1 span.text1{display:block;margin-bottom:10px;cursor:pointer}.footer1 .bottom-bar{border-top:1px solid rgba(0,0,0,.1)}.footer1 .bottom-bar .bottom-bar-content{padding:20px;display:flex;align-items:center;flex-direction:column;text-align:center;gap:10px}@media only screen and (min-width: 768px){.footer1 .bottom-bar .bottom-bar-content{padding:0;height:60px;max-width:1200px;margin:0 auto;flex-direction:row;justify-content:space-between;text-align:left}}.footer1 .bottom-bar .bottom-bar-content .text1{font-size:12px;color:rgba(0,0,0,.5)}